<template>
    <div class="data-page">
        <el-tabs v-model="dataType" @tab-click="handleClick">
            <el-tab-pane label="楼栋数据" name="1">
                <vRelaList v-if="dataType=='1'" ref="vRelaList" :tableData="tableData" :header="header" :recordCount="recordCount" @onTableRow="handleCurrentChange" @onPageChange="handlePageChange">
                    <div class="header-btn" slot="header">
                        <el-switch v-model="isRelaData" active-color="#13ce66" inactive-color="#ff4949"
                            active-text="已关联的数据" inactive-text="未关联的数据">
                        </el-switch>
                        <el-button size="small" type="primary" @click="handleRelaBuild">关联数据</el-button>
                    </div>
                    <div class="right-map" slot="map">
                        <vBuildDataMap ref="vBuildDataMap" :disCodeOption="disCodeOption" :isShowCode="isShowCode"></vBuildDataMap>
                    </div>
                </vRelaList>
            </el-tab-pane>
            <el-tab-pane label="户数据" name="2">
                <vRelaList v-if="dataType=='2'" ref="vRelaList" :tableData="tableData" :header="header" :recordCount="recordCount" :isCheck="true" 
                    @onTableSelect="handleSelectionChange" @onPageChange="handlePageChange">
                    <div class="rela-type" slot="header">
                        <el-switch v-model="isRelaData" active-color="#13ce66" inactive-color="#ff4949"
                            active-text="已关联的数据" inactive-text="未关联的数据">
                        </el-switch>
                    </div>
                    <techo-card slot="right-pane">
                        <div slot="header">请选择楼栋</div>
                        <techo-house ref="vCheckedHouse" :disCodeOption="disCodeOption" :buildOptions="buildOptions" 
                            @onRelaData="handleRelaHouse" @onQueryBuild="handleQueryBuildList"></techo-house>
                    </techo-card>
                </vRelaList>
            </el-tab-pane>
            <el-tab-pane label="流动人口数据" name="3">
                <vRelaList v-if="dataType=='3'" ref="vRelaList" :tableData="tableData" :header="header" :recordCount="recordCount" :isCheck="true" :isShowView="true" 
                    @onTableSelect="handleSelectionChange" @onPageChange="handlePageChange" @onView="handleView">
                    <div class="rela-type" slot="header">
                        <el-switch v-model="isRelaData" active-color="#13ce66" inactive-color="#ff4949"
                            active-text="已关联的数据" inactive-text="未关联的数据">
                        </el-switch>
                    </div>
                    <techo-card slot="right-pane">
                        <div slot="header">请选择户</div>
                        <techo-house ref="vCheckedHouse" :isShowHouse="true" :disCodeOption="disCodeOption" :buildOptions="buildOptions" :houseOptions="houseOptions" 
                            @onRelaData="handleRelaFlowHouse" @onQueryBuild="handleQueryBuildList" @onQueryHouse="handleQueryHouseList"></techo-house>
                    </techo-card>
                </vRelaList>
            </el-tab-pane>
            <el-tab-pane label="户籍人口数据" name="4">
                <vRelaList v-if="dataType=='4'" ref="vRelaList" :tableData="tableData" :header="header" :recordCount="recordCount" :isCheck="true" :isShowView="true" 
                    @onTableSelect="handleSelectionChange" @onPageChange="handlePageChange" @onView="handleView">
                    <div class="rela-type" slot="header">
                        <el-switch v-model="isRelaData" active-color="#13ce66" inactive-color="#ff4949"
                            active-text="已关联的数据" inactive-text="未关联的数据">
                        </el-switch>
                    </div>
                    <techo-card slot="right-pane">
                        <div slot="header">请选择户</div>
                        <techo-house ref="vCheckedHouse" :isShowHouse="true" :disCodeOption="disCodeOption" :buildOptions="buildOptions" :houseOptions="houseOptions" 
                            @onRelaData="handleRelaPeopleHouse" @onQueryBuild="handleQueryBuildList" @onQueryHouse="handleQueryHouseList"></techo-house>
                    </techo-card>
                </vRelaList>
            </el-tab-pane>
            <el-tab-pane label="基础人口数据" name="5">
                <vRelaList v-if="dataType=='5'" ref="vRelaList" :tableData="tableData" :header="header" :recordCount="recordCount" :isCheck="true" :isShowView="true" 
                    @onTableSelect="handleSelectionChange" @onPageChange="handlePageChange" @onView="handleView">
                    <div class="header-btn choose" slot="header">
                        <el-switch v-model="isRelaData" active-color="#13ce66" inactive-color="#ff4949"
                            active-text="已关联的数据" inactive-text="未关联的数据">
                        </el-switch>
                        <el-switch style="position: absolute;right: 0;" v-model="isRelaHouseId" active-color="#13ce66" inactive-color="#ff4949"
                            active-text="关联为户籍人口" inactive-text="关联为流入人口">
                        </el-switch>
                    </div>
                    <techo-card slot="right-pane">
                        <div slot="header">请选择户</div>
                        <techo-house ref="vCheckedHouse" :isShowHouse="true" :disCodeOption="disCodeOption" :buildOptions="buildOptions" :houseOptions="houseOptions" 
                            @onRelaData="handleRelaBasePeopleHouse" @onQueryBuild="handleQueryBuildList" @onQueryHouse="handleQueryHouseList"></techo-house>
                    </techo-card>
                </vRelaList>
            </el-tab-pane>
        </el-tabs>
        <techo-details v-if="dlgVisible" ref="details" :configKey="configKey" :height="'60%'"></techo-details>
    </div>
</template>

<script>
import service from "./services/data_service.js";

import vRelaList from "./components/rela-list.vue";
import vBuildDataMap from "./components/build-map.vue";

import config from "./config/config.js";
export default {
  components: {
    vRelaList,
    vBuildDataMap
  },
  data() {
    return {
      dataType: "1", // 关联的数据类型 1 楼栋数据 2 户数据 3 流动人口数据 4 户籍人口数据
      tableData: [],
      currentRow: null,
      tableSelection: [],
      recordCount: 0,
      disCodeOption: [],
      buildOptions: [],
      houseOptions: [],
      isShowCode: ["6102"], // 数默认展开第一级
      queryUrl: config[1].queryUrl.noRelaUrl[0],
      header: config[1].header,
      // 详情页面是否显示
      dlgVisible: false,
      configKey: config[1].configKey || "",

      isRelaHouseId: true, // 是否关联户籍地

      isRelaData: false, // 是否为已关联数据
    };
  },

  watch: {
    isRelaData(val) {
      if (val) {
        // 已关联数据
        if (this.dataType == 5) {
          this.queryUrl =
            this.isRelaHouseId == true
              ? config[5].queryUrl.relaUrl[0]
              : config[5].queryUrl.relaUrl[1];
        } else {
          this.queryUrl = config[this.dataType].queryUrl.relaUrl[0];
        }
      } else {
        // 未关联数据
        if (this.dataType == 5) {
          this.queryUrl =
            this.isRelaHouseId == true
              ? config[5].queryUrl.noRelaUrl[0]
              : config[5].queryUrl.noRelaUrl[1];
        } else {
          this.queryUrl = config[this.dataType].queryUrl.noRelaUrl[0];
        }
      }
      this.handleQueryRelaData();
    },
    isRelaHouseId(val) {
      if (this.isRelaData == true) {
        // 已关联数据
        this.queryUrl =
          val == true
            ? config[5].queryUrl.relaUrl[0]
            : config[5].queryUrl.relaUrl[1];
      } else {
        // 未关联数据
        this.queryUrl =
          val == true
            ? config[5].queryUrl.noRelaUrl[0]
            : config[5].queryUrl.noRelaUrl[1];
      }
      this.handleQueryRelaData();
    },
  },

  mounted() {
    service.requestGridCode(data => {
      this.disCodeOption = [data];
      this.isShowCode = [data.code];
    });
    this.handleQueryRelaData();
  },

  methods: {
    handleCurrentChange(val) {
      this.currentRow = val;
    },
    handleSelectionChange(value) {
      this.tableSelection = value;
    },
    /** 查看信息 */
    handleView(index, row) {
      this.dlgVisible = true;
      this.$nextTick(function() {
        this.$refs["details"].data = row;
        this.$refs["details"].visible = true;
      });
    },
    /** 查询楼栋列表 */
    handleQueryBuildList(code) {
      this.buildOptions = [];
      this.houseOptions = [];
      code != undefined && service.requestCodeByBuild(code, data => {
        this.buildOptions = data.list;
      });
    },
    /** 查询户列表 */
    handleQueryHouseList(param) {
      this.houseOptions = [];
      if (param.buildingId != "") {
        service.requestBuildByHouse(param, data => {
          this.houseOptions = data.list;
        });
      }
    },
    /** 切换tabs */
    handleClick(tab) {
      this.isRelaData = false;
      this.isRelaHouseId = true;
      this.queryUrl = config[tab.name].queryUrl.noRelaUrl[0];
      this.header = config[tab.name].header;
      this.configKey = config[tab.name].configKey || "";
      this.handleQueryRelaData();
    },
    /** 页码改变后执行的函数 */
    handlePageChange() {
      this.tableData = [];
      this.handleQueryRelaData();
    },
    /** 查询关联数据 */
    handleQueryRelaData() {
      let pageInfo = this.$refs["vRelaList"].getPageInfo();
      pageInfo.queryUrl = this.queryUrl;
      service.requestQueryRelaDataList(pageInfo, data => {
        if (this.dataType == "1") {
          _.map(data.list, item => {
            item.dicMap = {
              type: item.type == "1" ? "楼房" : "平房"
            };
          });
        }
        if (data.totalCount != 0 && data.list.length == 0) {
          pageInfo.page = data.totalPage;
          service.requestQueryRelaDataList(pageInfo, data => {
            if (this.dataType == "1") {
              _.map(data.list, item => {
                item.dicMap = {
                  type: item.type == "1" ? "楼房" : "平房"
                };
              });
            }
            this.recordCount = data.totalCount;
            this.tableData = data.list;
          });
        }
        this.recordCount = data.totalCount;
        this.tableData = data.list;
      });
    },
    /** 关联楼栋数据 */
    handleRelaBuild() {
      if (this.currentRow == null) {
        this.$popup.showNotification({
          type: "info",
          message: "请选择楼栋数据！"
        });
      } else if (this.$refs["vBuildDataMap"].buildingCode == "") {
        this.$popup.showNotification({
          type: "info",
          message: "请在地图上选择楼栋数据！"
        });
      } else {
        /** 关联楼栋数据 */
        service.requestRelaBuildData(
          [
            {
              buildingCode: this.$refs["vBuildDataMap"].buildingCode,
              buildingId: this.currentRow.id
            }
          ],
          data => {
            this.$popup.showNotification({
              type: "success",
              message: "楼栋数据关联成功！"
            });
            this.$refs["vBuildDataMap"].resetBuildMap();
            this.currentRow = null;
            this.handleQueryRelaData();
            this.$refs["vCheckedHouse"].resetForm();
          }
        );
      }
    },
    /** 关联户信息 */
    handleRelaHouse(buildForm) {
      if (this.tableSelection.length == 0) {
        this.$popup.showNotification({
          type: "info",
          message: "请选择需要关联的户！"
        });
      } else {
        let param = _.map(this.tableSelection, item => {
          return {
            buildingId: buildForm.buildingId,
            houseId: item.id
          };
        });
        service.requestRelaHouseData(param, data => {
          this.$popup.showNotification({
            type: "success",
            message: "楼栋数据关联成功！"
          });
          this.tableSelection = [];
          this.handleQueryRelaData();
          this.$refs["vCheckedHouse"].resetForm();
        });
      }
    },
    /** 关联流动人口数据 */
    handleRelaFlowHouse(buildForm) {
      if (this.tableSelection.length == 0) {
        this.$popup.showNotification({
          type: "info",
          message: "请选择需要关联的流动人口！"
        });
      } else {
        let param = _.map(this.tableSelection, item => {
          return {
            liveHouseId: buildForm.houseId,
            peopleId: item.peopleId
          };
        });
        service.requestRelaPeopleData(param, data => {
          this.$popup.showNotification({
            type: "success",
            message: "流动人口数据关联成功！"
          });
          this.tableSelection = [];
          this.handleQueryRelaData();
          this.$refs["vCheckedHouse"].resetForm();
        });
      }
    },
    /** 关联户籍人口数据 */
    handleRelaPeopleHouse(buildForm) {
      if (this.tableSelection.length == 0) {
        this.$popup.showNotification({
          type: "info",
          message: "请选择需要关联的户籍人口！"
        });
      } else {
        let param = _.map(this.tableSelection, item => {
          return {
            houseId: buildForm.houseId,
            peopleId: item.peopleId
          };
        });
        service.requestRelaPeopleData(param, data => {
          this.$popup.showNotification({
            type: "success",
            message: "户籍人口数据关联成功！"
          });
          this.tableSelection = [];
          this.handleQueryRelaData();
          this.$refs["vCheckedHouse"].resetForm();
        });
      }
    },
    handleRelaBasePeopleHouse(buildForm) {
      let that = this;
      if (this.tableSelection.length == 0) {
        this.$popup.showNotification({
          type: "info",
          message: "请选择需要关联的人口数据！"
        });
      } else {
        let param =
          this.isRelaHouseId == true
            ? _.map(this.tableSelection, item => {
                return {
                  houseId: buildForm.houseId,
                  peopleId: item.id
                };
              })
            : _.map(this.tableSelection, item => {
                return {
                  liveHouseId: buildForm.houseId,
                  peopleId: item.id
                };
              });
        service.requestRelaPeopleData(param, data => {
          this.$popup.showNotification({
            type: "success",
            message: "人口数据关联成功！"
          });
          this.tableSelection = [];
          this.handleQueryRelaData();
          this.$refs["vCheckedHouse"].resetForm();
        });
      }
    }
  }
};
</script>

<style lang="scss">
@import "./styles/data-connect.scss";
</style>
