import request from '@/plugin/axios';
import publicService from "@/api/sys/service.js";

const service = {
    ...publicService,
    /**
     * 分页查询导入任务
     * 
     * @param {any} param 分页查询参数
     * @param {any} successFunc 回调函数
     */
    requestImportTask(param, successFunc) {
        request({
            url: "/compre/imp/find/self?page="+ param.page +"&pageSize="+ param.pageSize + "&dataType="+ param.dataType,
            method: "get",
        }).then((data) => {
            successFunc(data);
        })
    },
    /**
     * 提交导入任务
     * 
     * @param {any} param 分页查询参数
     * @param {any} successFunc 回调函数
     */
    requestSaveTask(param, successFunc) {
        request({
            url: "/compre/imp/data/"+ param.url +"?districtCode="+ param.districtCode +"&fileId="+ param.fileId +"&name="+ param.name +"&key=" + param.key,
            method: "post",
        }).then((data) => {
            successFunc(data);
        })
    },
    /**
     * 分页查询未关联的数据
     * 
     * @param {any} param 查询参数
     * @param {any} successFunc 回调函数
     */
    requestQueryRelaDataList(param, successFunc) {
        request({
            url: "/compre/hp/"+ param.queryUrl +"?page="+ param.page +"&pageSize="+ param.pageSize,
            method: "get",
        }).then((data) => {
            successFunc(data);
        })
    },
    /**
     * 关联楼栋数据
     * 
     * @param {any} param 楼栋关联参数
     * @param {any} successFunc 回调函数
     */
    requestRelaBuildData(param, successFunc) {
        request({
            url: "/compre/hp/buildinfo/relaBuildingCode",
            data: param,
            method: "post",
        }).then((data) => {
            successFunc(data);
        })
    },
    /**
     * 关联户数据
     * 
     * @param {any} param 户关联参数
     * @param {any} successFunc 回调函数
     */
    requestRelaHouseData(param, successFunc) {
        request({
            url: "/compre/hp/house/relaBuildingCode",
            data: param,
            method: "post",
        }).then((data) => {
            successFunc(data);
        })
    },
    /**
     * 关联人口数据
     * 
     * @param {any} param 人口数据关联参数
     * @param {any} successFunc 回调函数
     */
    requestRelaPeopleData(param, successFunc) {
        request({
            url: "/compre/hp/people/rela",
            data: param,
            method: "post",
        }).then((data) => {
            successFunc(data);
        })
    }
}

export default service;