<template>
    <div class="building-map">
        <vBaseMap ref="map" @onLoad="handleMapLoaded"></vBaseMap>
        <div class="techo-tree-wrapper build-tree">
            <el-input placeholder="输入行政区关键字" size="small" v-model="filterText" clearable></el-input>
            <happy-scroll size="10" resize>
                <el-tree ref="tree" class="dis-code" :data="disCodeOption" :default-expanded-keys="isShowCode" node-key="code" accordion :filter-node-method="filterNode" highlight-current
                @node-click="handleNodeClick" :expand-on-click-node="false"></el-tree>
            </happy-scroll>
            <div v-if="buildingName!=''" class="build-name">楼栋名称：{{ buildingName }}</div>
        </div>
    </div>
</template>

<script>
import turfCenterMass from "@turf/center-of-mass";
import vBaseMap from "@/map/base-map.vue";
import request from "@/plugin/axios";

export default {
  components: {
    vBaseMap
  },
  props: {
    disCodeOption: {
      type: Array,
      default() {
        return [];
      }
    },
    isShowCode: {
      type: Array,
      default: ["6102"]
    }
  },
  data() {
    return {
      layerName: "",
      mapApi: null,
      layers: [],
      filterText: "",
      disCode: constants.REGION.CODE,
      buildingName: "",
      buildingCode: ""
    };
  },
  watch: {
    filterText(val) {
      this.$refs.tree.filter(val);
    },
    disCode(value) {
      this.handleShow();
    }
  },

  mounted() {
    let that = this;
    this.mapApi = this.$refs["map"].mapApi;
    this.layers = _.filter(this.mapApi.mapConfig.initLayers, item => {
      return item.id.indexOf("2") == 0;
    });

    // 地图比例缩放回调函数
    this.mapApi.mapOper.addMapEvent("zoomend", function(zoom) {
      that.handleMapZoom(zoom);
    });
  },

  methods: {
    /** 节点过滤 */
    filterNode(value, data) {
      if (!value) return true;
      return data.label.indexOf(value) !== -1;
    },
    /** 节点被点击 */
    handleNodeClick(node) {
      this.disCode = node.code;
    },
    /** 显示区域图层 */
    handleShow() {
      let codeLength = this.disCode.length;
      let zoom = 0;
      switch (codeLength) {
        case 4:
          this.layerName = "基础图层-市界";
          zoom = 8;
          break;
        case 6:
          this.layerName = "基础图层-区界";
          zoom = 9;
          break;
        case 9:
          this.layerName = "基础图层-街道办";
          zoom = 12;
          break;
        case 12:
          this.layerName = "基础图层-社区";
          zoom = 14;
          break;
        case 14:
          this.layerName = "基础图层-网格";
          zoom = 14.5;
          break;
        default:
          break;
      }
      this.getLayerConter(zoom);
      // 显示图层
      this.currentLayer();
    },
    /** 地图缩放完成事件 */
    handleMapZoom(zoom) {
      if (zoom <= 8) {
        this.layerName = "基础图层-市界";
      } else if (zoom <= 9) {
        this.layerName = "基础图层-区界";
      } else if (zoom <= 12) {
        this.layerName = "基础图层-街道办";
      } else if (zoom <= 14) {
        this.layerName = "基础图层-社区";
      } else if (zoom <= 14.5) {
        this.layerName = "基础图层-网格";
      }

      // 设置建筑物标注图层是否显示
      if (zoom > 14.5) {
        this.mapApi.layerApi.setLayerVisibility("301_Text", true);
      } else {
        this.mapApi.layerApi.setLayerVisibility("301_Text");
      }
      // 显示图层
      this.currentLayer();
    },
    /** 获取图层中心点并缩放至图层 */
    getLayerConter(zoom) {
      request({
        url: _.find(this.layers, ["name", this.layerName]).url,
        method: "get"
      }).then(res => {
        let feature = _.find(res.features, item => {
          return this.disCode == item.properties.CODE;
        });
        let lonLat = turfCenterMass(turfCenterMass(feature.geometry)).geometry
          .coordinates;
        this.mapApi.mapOper.setCenterAndZoom(lonLat, zoom);
      });
    },

    /** 当前显示图层 */
    currentLayer() {
      let that = this;
      // 设置图层显隐
      _.map(this.layers, item => {
        if (item.name.indexOf(that.layerName) == 0) {
          this.mapApi.layerApi.setLayerVisibility(item.id, true);
        } else {
          this.mapApi.layerApi.setLayerVisibility(item.id, false);
        }
      });

      let nId = _.find(this.layers, ["name", that.layerName]).id;
      this.mapApi.mapOper.activeFeatureHight(nId);
      // 设置当前选中区域高亮
      this.$nextTick(() => {
        let features = that.mapApi.map.queryRenderedFeatures({
          layers: [nId],
          filter: ["==", "CODE", this.disCode]
        });
        if (features.length > 0) {
          that.mapApi.mapOper.setActivedFeature(nId, features[0].id);
        }
      });
    },
    /** 地图加载完成 */
    handleMapLoaded() {
      let that = this;
      /** 默认显示第一级别 */
      this.handleShow();

      // 初始化标注图层
      this.mapApi.mapOper.initTextLayers();

      // 设置楼栋单击事件
      this.mapApi.mapOper.activeFeatureClick("301", feature => {
        this.buildingCode = feature.properties.CODE;
        this.buildingName = feature.properties.NAME;
      });
    },

    resetBuildMap() {
      this.layerName = "";
      this.filterText = "";
      this.disCode = constants.REGION.CODE;
      this.buildingName = "";
      this.buildingCode = "";
    }
  }
};
</script>

<style lang="scss">
.building-map {
  width: 100%;
  height: 100%;
  .build-tree {
    position: absolute;
    top: 0;
    height: 240px;
    .build-name {
      padding: 5px;
      margin: 10px 0;
      background-color: #fff;
    }
    .happy-scroll {
      height: calc(100% - 27px);
      margin-top: 5px;
      background-color: #fff;
    }
  }
}
</style>

