<template>
    <div class="data-list">
        <div class="build-table">
            <slot name="header"></slot>
            <el-table ref="table" size="medium" :data="tableData" border highlight-current-row tooltip-effect="dark" stripe :height="$slots.header ? 'calc(100% - 86px)' : 'calc(100% - 44px)'"
                @current-change="handleCurrentChange" @selection-change="handleSelectionChange" @row-click="handleRowClick" header-cell-class-name="techo-list__body-table-header">
                <el-table-column v-if="isCheck" type="selection" width="40" align="center"></el-table-column>
                <el-table-column type="index" label="序号" width="50" align="center"></el-table-column>
                <el-table-column v-for="(value, key, index) in header" :key="index" :label="key" :prop="value"
                    align="center" show-overflow-tooltip>
                </el-table-column>
                <el-table-column v-if="isShowView" label="操作" fixed="right" align="center" width="130">
                    <template slot-scope="scope">
                        <el-button size="mini" class="techo-list__body-table-button-view" @click.stop="handleView(scope.$index,scope.row)">查看</el-button>
                    </template>
                </el-table-column>
            </el-table>
            <div class="techo-list__pager">
                <el-pagination ref="pager" @size-change="handlePagerChange" @current-change="handlePagerChange"
                    :page-sizes="pageSizes" layout="total, sizes, prev, pager, next, jumper" :total="recordCount">
                </el-pagination>
            </div>
        </div>
        <slot name="map"></slot>
        <slot name="right-pane"></slot>
    </div>
</template>

<script>
export default {
  props: {
    tableData: {
      type: Array,
      default: []
    },
    header: {
      type: Object,
      default: {}
    },
    // 总记录数
    recordCount: {
      type: Number,
      default: 0
    },
    // 每页记录数
    pageSizes: {
      type: Array,
      default() {
        return [10, 20, 30, 50, 100];
      }
    },
    isCheck: {
      type: Boolean,
      default: false
    },
    isShowView: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {};
  },

  methods: {
    /** 点击行选中 */
    handleRowClick(row) {
      this.$refs.table.toggleRowSelection(row);
    },
    /** 列表选择集改变 */
    handleSelectionChange(value) {
      this.$emit("onTableSelect", value);
    },
    handleCurrentChange(val) {
      this.$emit("onTableRow", val);
    },
    handleView(index, row) {
      this.$emit("onView", index, row);
    },
    // 获取分页信息
    getPageInfo() {
      let page, pageSize;

      if (this.$refs["pager"] == undefined) {
        page = 1;
        pageSize = 10;
      } else {
        page = this.$refs["pager"].internalCurrentPage;
        pageSize = this.$refs["pager"].internalPageSize;
      }

      return {
        page,
        pageSize
      };
    },
    // 分页控件改变触发事件
    handlePagerChange() {
      this.$emit("onPageChange", this.getPageInfo());
    }
  }
};
</script>

